  box-shadow: 0 8px 16px -8px rgba(0, 0, 0, 0.2);
}
.shadow_l {
  -webkit-box-shadow: 0 20px 24px -12px rgba(0, 0, 0, 0.2);
  box-shadow: 0 20px 24px -12px rgba(0, 0, 0, 0.2);
}
#root {
  height: 100%;
}
.wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  background-color: var(--gray-000);
}
@media screen and (max-width: 1023px) {
  .wrap {
    overflow: initial;
  }
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  position: relative;
  width: 100%;
}
::-webkit-scrollbar {
  width: 3px;
  height: 3px;
}
::-webkit-scrollbar-thumb {
  background: var(--orange-alpha-dark);
  border-radius: 2px;
}
::-webkit-scrollbar-track {
  background: 0 0;
}
.dimmed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: 30;
  background-color: rgba(0, 0, 0, 0.25);
}
.br_pc {
  display: block;
}
@media screen and (max-width: 1023px) {
  .br_pc {
    display: none;
  }
}
.br_mo {
  display: none;
}
@media screen and (max-width: 1023px) {
  .br_mo {
    display: block;
  }
}
.team_overview_page .container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 72px 40px 72px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .container {
    max-width: none;
    padding: 0;
  }
}
@media screen and (max-width: 1023px) {
  .team_overview_page .content {
    padding-bottom: 50px;
  }
}
.team_overview_page .box_top .project_area {
  margin-top: 0;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .box_top .project_area .title_group {
    display: none;
  }
}
@media screen and (max-width: 1023px) {
  .team_overview_page .box_top .search {
    margin-top: 0;
  }
}
.team_overview_page .project_area {
  margin-top: 72px;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .project_area .title_group {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
@media screen and (max-width: 1023px) {
  .team_overview_page .project_area .title_group .btn_toggle {
    display: none;
  }
}
.team_overview_page .banner_box {
  margin-top: 48px;
}
.team_overview_page .placeholder_box {
  margin-top: 24px;
}
.team_overview_page .card {
  display: none;
  margin-top: 25px;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .card {
    display: block;
  }
}
@media screen and (max-width: 639px) {
  .team_overview_page .card {
    margin-top: 16px;
  }
}
.team_overview_page .card.is_active {
  display: block;
}
.team_overview_page .project_table {
  display: none;
  margin-top: 24px;
}
.team_overview_page .project_table.is_active {
  display: block;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .project_table.is_active {
    display: none;
  }
}
.team_overview_page .usage {
  margin-top: 48px;
}
@media screen and (max-width: 1023px) {
  .team_overview_page .usage {
    margin-top: 32px;
  }
}
@media screen and (max-width: 639px) {
  .team_overview_page .usage {
    margin-top: 24px;
  }
}
.team_overview_page .usage + .project_area {
  margin-top: 72px;
}
